<script setup lang="ts">
import { ref, watch } from 'vue';
import { ElMessage, ElForm } from 'element-plus';
import { save } from "@/api/api.ts";
import QRCode from 'qrcode';

const visible = ref(false);
const formRef = ref<InstanceType<typeof ElForm>>();

const form = ref({
  id: 0,
  channel_name: "",  // 
  product_code: "",  // 
  payment_amount: 0,  // ()
});

// 
const testResult = ref({
  success: false,
  message: "",
  request_params: {},  // 
  channel_response: {},  // 
  qr_code_url: "",  // URL
});

const qrCodeUrl = ref("");  //  URL

// 
const rules = ref({
  channel_name: [{ required: true, message: '', trigger: 'blur' }],
  payment_amount: [
    {
      required: true,
      message: '',
      trigger: 'blur',
      validator: (_rule: any, value: any, callback: any) => {
        if (!value || isNaN(Number(value)) || Number(value) <= 0) {
          callback(new Error(''));
        } else {
          callback();
        }
      },
    },
  ],
});

// 
const open = async (data = {}) => {
  // 
  resetForm();
  resetTestResult();

  const merged = { id: 0, ...data };
  Object.assign(form.value, merged);
  visible.value = true;
};

// 
const handleSubmit = async () => {
  await formRef.value?.validate();

  let submitData: any = {};
  try {
    // 
    resetTestResult();
    // 
    const amount = Number(form.value.payment_amount);
    submitData = {
      channel_id: form.value.id || 0,
      channel_name: form.value.channel_name?.trim?.() || form.value.channel_name,
      product_code: form.value.product_code?.trim?.() || form.value.product_code,
      payment_amount: amount > 0 ? amount : 1,
    };

    // 
    Object.keys(submitData).forEach(key => {
      if (submitData[key] === undefined || submitData[key] === '' || submitData[key] === null) {
        delete submitData[key];
      }
    });

    let res: any = await save('/api/v1/admin/payment-channel/test', submitData);
    
    //  data.data.status 
    const isSuccess = res.data?.data?.status === true;
    
    // 
    testResult.value = {
      success: isSuccess,
      message: res.data?.data?.message || res.data?.message || res.message || (isSuccess ? '' : ''),
      request_params: res.data?.header || {},
      channel_response: res.data?.raw_response?.original_response || res.data?.data || {},
      qr_code_url: res.data?.data?.qr_code || res.data?.url || "",
    };

    if (isSuccess) {
      ElMessage.success(res.data?.data?.message || res.data?.message || res.message || '');

      // URL
      if (res.data?.data?.qr_code || res.data?.url) {
        generateQRCode(res.data?.data?.qr_code || res.data?.url);
      }
    } else {
      ElMessage.error(res.data?.data?.message || res.data?.message || res.message || '');
    }
  } catch (error: any) {
    console.error(':', error);
    testResult.value = {
      success: false,
      message: '' + (error.message || ''),
      request_params: submitData,
      channel_response: {},
      qr_code_url: "",
    };
    ElMessage.error('');
  }
};

// 
const generateQRCode = (url: string) => {
  QRCode.toDataURL(url, (err, url) => {
    if (err) {
      ElMessage.error('');
      console.error(err);
      return;
    }
    qrCodeUrl.value = url;  //  URL
  });
};

// 
const resetForm = () => {
  form.value = {
    id: 0,
    channel_name: "",
    product_code: "",
    payment_amount: 1,
  };
};

// 
const resetTestResult = () => {
  testResult.value = {
    success: false,
    message: "",
    request_params: {},
    channel_response: {},
    qr_code_url: "",
  };
  qrCodeUrl.value = "";
};

//  visible 
watch(visible, (newVal, oldVal) => {
  if (oldVal === true && newVal === false) {
    resetForm();
    resetTestResult();
  }
});

defineExpose({ open });
</script>

<template>
  <el-drawer v-model="visible" title="" size="70%">
    <el-form :model="form" :rules="rules" ref="formRef" label-width="120px">
      <el-divider content-position="left"></el-divider>

      <!--  -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="" prop="channel_name">
            <el-input v-model="form.channel_name" placeholder="" />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <el-input v-model="form.product_code" placeholder="" disabled />
          </el-form-item>
        </el-col>
      </el-row>

      <!--  -->
      <el-form-item label="()" prop="payment_amount">
        <el-input v-model="form.payment_amount" placeholder="">
          <template #append></template>
        </el-input>
      </el-form-item>

      <!--  -->
      <el-form-item>
        <el-button type="primary" @click="handleSubmit" :loading="false"></el-button>
      </el-form-item>
    </el-form>

    <!--  -->
    <div class="test-result-container">
      <el-divider content-position="left"></el-divider>

      <!--  -->
      <el-form-item label="">
        <el-input
            v-model="testResult.message"
            type="textarea"
            :rows="3"
            placeholder=""
            readonly
        />
      </el-form-item>

      <!--  -->
      <el-form-item label="">
        <el-input
            :value="JSON.stringify(testResult.request_params, null, 2)"
            type="textarea"
            :rows="6"
            placeholder=""
            readonly
            class="json-display"
        />
      </el-form-item>

      <!--  -->
      <el-form-item label="">
        <el-input
            :value="JSON.stringify(testResult.channel_response, null, 2)"
            type="textarea"
            :rows="6"
            placeholder=""
            readonly
            class="json-display"
        />
      </el-form-item>

      <!--  -->
      <div v-if="qrCodeUrl" class="qrcode-container">
        <h4></h4>
        <img :src="qrCodeUrl" alt="QRCode" />
      </div>
    </div>
  </el-drawer>
</template>

